"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, BarChart, Bar } from "recharts";

// Mock data - in production, this would come from the CRM API
const mockDashboardData = {
  customerName: "John Smith",
  currentBalance: -45.67, // Negative means in credit
  nextPaymentDate: "2024-02-15",
  accountNumber: "UK123456789",
  usageHistory: [
    { month: "Oct", usage: 450, cost: 89.50 },
    { month: "Nov", usage: 520, cost: 103.40 },
    { month: "Dec", usage: 680, cost: 135.20 },
    { month: "Jan", usage: 720, cost: 143.10 },
  ],
  recentBills: [
    { date: "2024-01-15", amount: 143.10, status: "Paid" },
    { date: "2023-12-15", amount: 135.20, status: "Paid" },
  ],
  meterReadings: [
    { type: "Electricity", lastReading: "12345", date: "2024-01-10" },
    { type: "Gas", lastReading: "6789", date: "2024-01-10" },
  ]
};

export default function Dashboard() {
  const [data, setData] = useState(mockDashboardData);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Simulate API call
    setTimeout(() => {
      setLoading(false);
    }, 1000);
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-lg">Loading your dashboard...</div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Welcome Header */}
      <div className="bg-white rounded-lg shadow p-6">
        <h1 className="text-3xl font-bold text-gray-900">Welcome back, {data.customerName}</h1>
        <p className="text-gray-600 mt-2">Account: {data.accountNumber}</p>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Current Balance</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">
              {data.currentBalance < 0 ? (
                <span className="text-green-600">£{Math.abs(data.currentBalance).toFixed(2)} in credit</span>
              ) : (
                <span className="text-red-600">£{data.currentBalance.toFixed(2)} due</span>
              )}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Next Payment Due</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-gray-900">{data.nextPaymentDate}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">This Month's Usage</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-gray-900">
              {data.usageHistory[data.usageHistory.length - 1]?.usage} kWh
            </div>
            <div className="text-sm text-gray-600">
              £{data.usageHistory[data.usageHistory.length - 1]?.cost}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Energy Usage Chart */}
      <Card>
        <CardHeader>
          <CardTitle>Energy Usage Over Time</CardTitle>
          <CardDescription>Your monthly energy consumption and costs</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={data.usageHistory}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="month" />
                <YAxis yAxisId="left" />
                <YAxis yAxisId="right" orientation="right" />
                <Tooltip />
                <Bar yAxisId="left" dataKey="usage" fill="#3b82f6" name="Usage (kWh)" />
                <Line yAxisId="right" type="monotone" dataKey="cost" stroke="#ef4444" strokeWidth={2} name="Cost (£)" />
              </LineChart>
            </ResponsiveContainer>
          </div>
        </CardContent>
      </Card>

      {/* Quick Actions */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <Button asChild className="h-20 flex-col">
          <a href="/billing">
            <div className="text-lg font-semibold">View Bills</div>
            <div className="text-sm opacity-90">Check your billing history</div>
          </a>
        </Button>
        
        <Button asChild variant="outline" className="h-20 flex-col">
          <a href="/meter-reading">
            <div className="text-lg font-semibold">Submit Reading</div>
            <div className="text-sm opacity-90">Update your meter readings</div>
          </a>
        </Button>
        
        <Button asChild variant="outline" className="h-20 flex-col">
          <a href="/payment">
            <div className="text-lg font-semibold">Make Payment</div>
            <div className="text-sm opacity-90">Pay your energy bill</div>
          </a>
        </Button>
        
        <Button asChild variant="outline" className="h-20 flex-col">
          <a href="/support">
            <div className="text-lg font-semibold">Get Help</div>
            <div className="text-sm opacity-90">Contact customer support</div>
          </a>
        </Button>
      </div>

      {/* Recent Activity */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Recent Bills</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {data.recentBills.map((bill, index) => (
                <div key={index} className="flex justify-between items-center p-3 bg-gray-50 rounded">
                  <div>
                    <div className="font-medium">{bill.date}</div>
                    <div className="text-sm text-gray-600">{bill.status}</div>
                  </div>
                  <div className="font-semibold">£{bill.amount}</div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Latest Meter Readings</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {data.meterReadings.map((reading, index) => (
                <div key={index} className="flex justify-between items-center p-3 bg-gray-50 rounded">
                  <div>
                    <div className="font-medium">{reading.type}</div>
                    <div className="text-sm text-gray-600">{reading.date}</div>
                  </div>
                  <div className="font-semibold">{reading.lastReading}</div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
